.App {
  text-align: center;
  flex-direction: row;
  display: flex;
  height: 100%;
}
.content {
  background-color: #16291a;
  font-size: calc(10px + 2vmin);
  color: #e8e7b5;
}
@media screen and (max-width: 600px) {
  .App {
    flex-direction: column;
  }
}
.headshot {
  flex: 0.9;
  max-width: 100%;
}
.left {
  float: 'left';
  flex:1;
}
.right {
  float: 'right';
  flex: 1;
  padding: 2.5vw;
}
.lists {
  display: flex;
  flex-direction: row;
  flex:1;
}
.list {
  flex: 1;
  width: 100%;
  text-align: left;
}
.list ul {
  list-style-type: none;
}
.tagline {
  font-size: calc(10px + 3vmin);
}
a {
  color: #faee45;
}